#claiming-social-security div[data-tooltip-name] {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}
#claiming-social-security *[data-tooltip-target] {
  cursor: pointer;
  border-bottom: none;
  color: var(--pacific);
}
#claiming-social-security #tooltip-container {
  background-color: var(--gray-10);
  border: 0 solid var(--black);
  display: none;
  font-style: normal;
  padding: 15px;
  position: absolute;
  width: 20em;
  z-index: 3000;
  /* These elements, .outertip and .innertip, make the little speech-balloon
   styled tip appear along the top of the tooltip. */
}
#claiming-social-security #tooltip-container .content {
  padding: 0;
  margin: 0;
}
#claiming-social-security #tooltip-container .content p:last-child {
  margin-bottom: 0;
}
#claiming-social-security #tooltip-container .outertip {
  border: 0 solid transparent;
  border-bottom-color: var(--black);
  content: " ";
  height: 0;
  position: absolute;
  left: -5px;
  top: -20px;
  width: 0;
}
#claiming-social-security #tooltip-container .innertip {
  border: 7px solid transparent;
  border-bottom-color: var(--gray-10);
  content: " ";
  height: 0;
  position: absolute;
  left: 8px;
  top: -14px;
  width: 0;
}
#claiming-social-security .tooltip-target {
  border-width: 0;
  border-style: dotted;
  border-color: var(--pacific);
  color: var(--pacific);
  cursor: pointer;
  text-decoration: none;
}

